@font-face {
  font-family: Monoton;
  src: url(../fonts/Monoton/Monoton-Regular.ttf);
}

:root {
  --background: #130a06;
  --text: #939597;
  --white: #e6e7e8;
  --black: #14141b;
  --red: #cf0020;
  --purple: #bf73d8;
  --green: #2fc358;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  touch-action: manipulation;
}

html {
  font: 18px monospace;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  color: var(--text);
  text-align: center;
}

img,
picture {
  max-width: 100%;
  display: block;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

html {
  background: var(--background);
  font: 18px sans-serif;
}

body {
  text-align: center;
}

.JOG {
  font-family: Monoton, sans-serif;
  letter-spacing: -0.04em;
  color: var(--white);
}

.J {
  color: var(--purple);
}

.G {
  color: var(--green);
}

h1 .JOG{
  font-size: 64px;
}

#L-plate {
  background: var(--white);
  color: var(--red);
  padding: 8px 0 8px 16px;
  line-height: 1;
  display: inline-block;
  transform: rotate(10deg);
  font-size: 64px;
}

h1,
h2 {
  text-transform: uppercase;
  letter-spacing: 0.3em;
  text-align: center;
  font-size: 48px;
  color: var(--white);
  margin: 0;
}

h2 {
  text-transform: uppercase;
  letter-spacing: 0.3em;
  text-align: center;
  font-size: 24px;
}

#score {
  color: var(--green);
  font-size: 32px;
  line-height: 0;
  margin: 18px 0 4px;
}

#score strong{
  color: var(--green);
}

#fredback{
  color: var(--green);
  font-size: 32px;
}

#question img{
  display: block;
  margin: 0 auto;
  width: 200px;
}

button {
  color: var(--white);
  background: transparent;
  border: 3px var(--purple) double;
  padding: 8px;
  text-transform: uppercase;
  letter-spacing: 0.4em;
  min-width: 16em;
  display: block;
  margin: 20px auto;
}

#answers button {
  text-transform: none;
  letter-spacing: 0;
  text-align: left;
  display: block;
  margin: 16px auto;
  width: 100%;
  max-width: 480px;
  font-size: 14px;
  border-radius: 8px;
  padding: 12px;
  height: 5em;
}

button:hover {
  background: var(--black);
}

#game{
  min-height: 100vh;
}

#game-over{
  margin: 20px 0;
}

#car {
  position: absolute;
  left: 0;
  bottom: 10px;
  transition: left 2s;
  width: 128px;
  height: 128px;
  background: url(../images/car.png) 0 0;
}

a:link,a:visited{
 color: var(--purple);
}

a:hover{
  color: var(--green);
}

footer p{
  font-size: 0.7em;
  margin: 0;
}

@media (max-width: 599px) {
  h1 .jog{
    font-size: 28px;
  }
  h1{
    font-size: 24px;
  }

  h2{
    font-size: 14px;
  }

  #L-plate{
    font-size: 24px;
    padding: 4px 0 4px 12px;
  }

  #score, #fredback{
    font-size: 14px;
  }

  #car{
    bottom: 0;
  }
}

